.neumorphism_card{
border-radius: 21px;
    background: linear-gradient(145deg, #cacaca, #f0f0f0);
    box-shadow: 35px 35px 70px #a8a8a8,
        -35px -35px 70px #ffffff;
}

.neumorphism_circle{
border-radius: 50%;
    background: linear-gradient(145deg, #d4d4d4, #fbfbfb);
    box-shadow: 25px 25px 50px #b0b0b0,
        -25px -25px 50px #ffffff;
}

#loading-bar-spinner.spinner {
    left: 50%;
    margin-left: -20px;
    top: 50%;
    margin-top: -20px;
    position: absolute;
    z-index: 19 !important;
    animation: loading-bar-spinner 400ms linear infinite;
}

#loading-bar-spinner.spinner .spinner-icon {
    width: 40px;
    height: 40px;
    border: solid 5px transparent;
    border-top-color: rgb(37, 63, 180) !important;
    border-left-color: rgb(37, 62, 171) !important;
    border-radius: 50%;
}

@keyframes loading-bar-spinner {
    0% {
        transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

#HomeCard{
    animation: ease-in-out 0.7s popup;
}



@keyframes popup {
    from {
       transform: translateY(80px);
       opacity: 0.5;
    }

    to {
        transform: translateY(0px);
       opacity: 1;
    }
}


.our-problem{
    display: inline-block;
    background-color: #ff6a00;
    background: -webkit-linear-gradient(0deg, oklch(67.3% 0.266 25.039656026515278), oklch(85.82% 0.201 91.19), #ca196f);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: phaser 3s infinite;
    animation-fill-mode: forwards;
    animation-direction: alternate;
    font-weight: bolder;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.loader-line {
    width: 200px;
    height: 3px;
    position: relative;
    overflow: hidden;
    background-color: #ddd;
    margin: 100px auto;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 20px;
}

.loader-line:before {
    content: "";
    position: absolute;
    left: -50%;
    height: 3px;
    width: 40%;
    background-color: coral;
    -webkit-animation: lineAnim 1s linear infinite;
    -moz-animation: lineAnim 1s linear infinite;
    animation: lineAnim 1s linear infinite;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 20px;
}

.bann_button{
border-radius: 78px;
    background: #d71d1d;
    box-shadow: 15px 15px 29px #c41a1a,
        -15px -15px 29px #ea2020;
}
.bann_button_active{
    border-radius: 78px;
        background: #159013;
        box-shadow: 20px 20px 60px #127a10,
            -20px -20px 60px #18a616;
}

.bann_button_inactive{
    border-radius: 78px;
        background: #e0e0e0;
        box-shadow: 20px 20px 60px #bebebe,
            -20px -20px 60px #ffffff;
}

.p-datepicker {
    border:1px solid black;
  }

@keyframes lineAnim {
    0% {
        left: -40%;
    }

    50% {
        left: 20%;
        width: 80%;
    }

    100% {
        left: 100%;
        width: 100%;
    }
}

@keyframes phaser {
    0% {
        background-position: 0% 0;
    }

    100% {
        background-position: 100% 0;
    }
}

